  <div>
    <div class="logo"">
    &nbsp;█████╗&nbsp;&nbsp;██████╗&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
    ██╔══██╗██╔════╝&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
    ╚█████╔╝███████╗&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
    ██╔══██╗██╔═══██╗&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
    ╚█████╔╝╚██████╔╝&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
    &nbsp;╚════╝&nbsp;&nbsp;╚═════╝&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
    &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
    ██████╗&nbsp;&nbsp;█████╗&nbsp;██████╗&nbsp;<br>
    ██╔══██╗██╔══██╗██╔══██╗<br>
    ██████╔╝███████║██████╔╝<br>
    ██╔══██╗██╔══██║██╔══██╗<br>
    ██████╔╝██║&nbsp;&nbsp;██║██║&nbsp;&nbsp;██║<br>
    ╚═════╝&nbsp;╚═╝&nbsp;&nbsp;╚═╝╚═╝&nbsp;&nbsp;╚═╝
    </div>
    <div class="about">
      <hgroup>
        <h3>a tip prompt for out-of-work bartenders.</h3>
        <h4>(like <router-link target="_blank" to="/Q2xhcmsgQnVya2hhcmR0fENsYXJrLUJ1cmtoYXJkdHwxMi4wMA%3D%3D">this</router-link>)</h4>
      </hgroup>

      <p>plug in your name, <a target="_blank" href="https://help.venmo.com/hc/en-us/articles/235432448-Check-or-Edit-Your-Username">venmo username <i class="fas fa-external-link-alt"></i></a>, and suggested tip amount below and 'copy link' for a direct-to-venmo tip prompt to share with your followers.</p>
    </div>
    <div class="bartender-input">
      <div class="bartender-input__set">
        <input id="name" v-model="bartender.name" v-on:input="validateForm" v-on:keydown="filterInput($event)" placeholder="Your name"><br>
        <input id="venmoUsername" v-model="bartender.venmoUsername" v-on:input="validateForm" v-on:keydown="filterInput($event)" placeholder="Your venmo username">
      </div>
      <label for="defaultTip">Suggested tip:</label> $<input id="defaultTip" v-model="bartender.tipAmount" v-on:blur="parseTip" v-on:input="validateForm" type="number" pattern="\d*" min="1" max="2999" step="1"><br>
      <transition name="fade">
        <p class="error" v-if="!tipAmountIsValid">Tips can be between $1 and $2999.</p>
      </transition>
    </div>


    <router-link :to="{ name: 'Bartender', params: { encodedBartender: bartender.encode() } }" class="pure-button  button button--view-page" :disabled="!formIsValid"><i class="far fa-eye"></i> view</router-link>
    <button class="pure-button  button button--copy-link" v-on:click="copyLink" :disabled="!formIsValid"><i class="far fa-clipboard"></i> copy link</button>
    <transition name="fade">
      <flash-message class="flash  flash--inline"></flash-message>
    </transition>



  </div>
</template>

<script>
  import {Bartender} from '../src/App'


  export default {
    name: 'home',
    metaInfo() {
      return {
        titleTemplate: null,
        title: 'eightysixbar',
        meta: [
          { name: 'description', content: 'a direct-to-venmo tip prompt for out-of-work-bartenders.' },
          { name: 'twitter:title', content: 'eightysixbar' },
          { name: 'twitter.description', content: 'a direct-to-venmo tip prompt for out-of-work-bartenders.' },
          { property: 'og:title', content: this.bartender.name + 'eightysixbar' },
          { property: 'og:description', content: 'a direct-to-venmo tip prompt for out-of-work bartenders.' },
          { property: 'og:url', content: 'https://eightysixbar.com/' }
        ]
      }
    },
    methods: {
      copyLink: function() {
        this.$copyText("https://eightysixbar.com/#/"+this.bartender.encode())
        this.flash('copied!', 'success', {
          timeout: 2800,
          important: true
        })
      },

      validateForm: function() {
        this.tipAmountIsValid = (this.bartender.tipAmount >= 1 && this.bartender.tipAmount < 2999.99)

        if (this.bartender.name && this.bartender.venmoUsername && this.tipAmountIsValid)
        {
          this.formIsValid = true
        }
        else {
          this.formIsValid = false
        }

      },
      parseTip: function() {
        this.bartender.tipAmount = parseInt(this.bartender.tipAmount)
      },
      filterInput: function(event) {
        var blacklist = ['|']

        if (event.target.id == 'venmoUsername') {
          blacklist.push(' ', '~', '!', '@', '#', '$', '^', '&', '%', '*', '(', ')', '+', '=', '{', '}', '[', ']', '\\', ':', ';', '"', '\'', '<', ',', '>', '.', '?', '/', '`', '~');
        }

        if (blacklist.includes(event.key)) {
          event.preventDefault();
        }
      }
    },
    data() {
      return {
        bartender: new Bartender('', '', 12.00),
        formIsValid: false,
        tipAmountIsValid: true
      }
    }
  }

</script>

