      &nbsp;╚═════╝&nbsp;╚═╝&nbsp;&nbsp;╚═╝╚═╝&nbsp;&nbsp;╚═╝<br>
    </router-link>

    <p class="receipt__info">{{ timestamp.toLocaleString() }}<br><span>Bartender</span>: {{ bartender.name }}</p>

    <div class="pure-g  receipt__summary">
      <div class="pure-u-1-8  summary-qty">{{ product.qty }}</div>
      <div class="pure-u-2-3  summary-item">{{ product.item }}</div>
      <div class="pure-u-5-24  summary-price">${{ product.price }}</div>

      <div class="pure-u-1-8  summary-qty">{{ product.qty }}</div>
      <div class="pure-u-2-3  summary-item"><i>COMP ITEM</i></div>
      <div class="pure-u-5-24  summary-price">-${{ product.price }}</div>
    </div>

    <div class="receipt__total">
      Total: $<input type="number" :readonly="true" value="0" min="1.00" max="2999.00">
    </div>

    <div class="receipt__tip">
      <b>Tip: $</b><input v-on:input="validateForm" v-model.number="tip.amount" type="number" pattern="\d*" min="1" max="2999" step="1"><br>
      <p class="error" v-if="!tipAmountIsValid">Tip must be between $1 and $2999.</p>
      <button class="tip-bartender  pure-button pure-button-primary button" v-on:click="tipBartender" :disabled="!formIsValid"><i class="fas fa-dollar-sign"></i> Pay with Venmo</button>
    </div>

    <div class="receipt__note">
      <div class="note">
        <label class="note__label" for="msg"><b><i>Note:</i></b></label>
        <textarea id="note-input" name="msg" class="note__field note__field--textarea" v-on:input="validateForm" v-on:keydown="filterInput($event)" v-on:keyup="sizeTextarea($event.target)" v-model.trim="tip.msg" v-focus>{{ tip.msg}}</textarea>
        <input  v-on:input="validateForm" v-model="drinkRequest.enabled" type="checkbox" name="drinkRequested" id="drink-requested">
        <label class="note__label  note__label--drink-request" v-if="!drinkRequest.enabled" for="drinkRequested">Could you help me make a drink with what I've got at home?</label>
        <textarea class="note__field note__field--textarea" v-on:keydown="filterInput($event)" v-on:keyup="sizeTextarea($event.target)" v-on:input="validateForm" v-if="drinkRequest.enabled" v-model.trim="drinkRequest.msg">{{ drinkRequest.msg }}<textarea> rows="8" cols="80"></textarea>
        <span class="note__label  note__label--ingredients" v-if="drinkRequest.enabled">Right now, I have:</span>
      </div>
