        <input-tag class="ingredient-list" v-focus v-on:input="validateForm" v-if="drinkRequest.enabled" v-model="drinkRequest.ingredients" placeholder="Type ingredients; separate by comma..." :add-tag-on-blur="true" :add-tag-on-keys="[13,188,190]"></input-tag>
      </div>
      <p class="char-count" v-if="noteLength > 1499" id="note-length"><span>{{ noteLength }}</span>/<span>2000</span></p>
    </div>

