<script>
  import { Bartender, Tip, DrinkRequest, venmo } from '../src/App.js'


  var products = [
    {
      qty: 4,
      item: 'Gansett 12oz',
      price: '12.00'
    },
    {
      qty: 3,
      item: 'Domaine Karên Chardonnay',
      price: '36.00'
    },
    {
      qty: 1,
      item: 'Bartender\'s Choice',
      price: '14.00'
    },
    {
      qty: 1,
      item: 'Old Fashioned',
      price: '11.00'
    },
    {
      qty: 1,
      item: 'Mai Tai',
      price: '14.00'
    },
    {
      qty: 1,
      item: 'Gin Fizz',
      price: '13.00'
    },
    {
      qty: 2,
      item: 'Espresso Martini',
      price: '22.00'
    },
    {
      qty: 1,
      item: 'Van Wrinkle 12yr Dram',
      price: '20.00'
    },
    {
      qty: 1,
      item: 'Cosmopolitan',
      price: '12.00'
    },
    {
      qty: 2,
      item: 'Lemon Drop Shot',
      price: '14.00'
    }
  ]

  export default {
    name: 'Bartender',
    metaInfo() {
      return {
        title: this.bartender.name,
        meta: [
          { name: 'description', content: 'Send a tip to ' + this.bartender.name + '!' },
          { name: 'twitter:title', content: this.bartender.name },
          { name: 'twitter.description', content: 'Send a tip to ' + this.bartender.name + '!' },
          { property: 'og:title', content: this.bartender.name },
          { property: 'og:description', content: 'Send a tip to ' + this.bartender.name + '!' },
          { property: 'og:url', content: 'https://eightysixbar.com/#/' + this.$route.params.encodedBartender }
        ]
      }
    },
    computed: {
      bartender() {
        try {
          return Bartender.decode(this.$route.params.encodedBartender)
        } catch (err) {
          this.$router.push({ name: 'PageNotFound' });
        }
      },
      tip() {
        return new Tip(this.bartender)
      }
    },
    data () {
      return {
        drinkRequest: new DrinkRequest(),
        noteLength: 7,
        formIsValid: true,
        tipAmountIsValid: true,
        timestamp: new Date(),
        product: products[Math.floor(Math.random() * Math.floor(products.length))],
        bartenderFirstName: function() {
          return this.bartender.name.split(' ')[0]
        }
      }
    },
    methods: {
      validateForm: function() {
        this.noteLength = this.tip.getMsg(this.drinkRequest).length;

        if (this.noteLength  > 2000) {
          this.formIsValid = false;
        } else if (this.tip.amount < 1 || this.tip.amount > 2999.99) {
          this.formIsValid = false;
          this.tipAmountIsValid = false;
        } else {
          this.formIsValid = true;
          this.tipAmountIsValid = true;
        }

        var charCountEl = document.getElementById('note-length');
        if (charCountEl) {
          if (this.noteLength > 2000 && !charCountEl.classList.contains('error')) {
            charCountEl.classList.add('error')
          } else if (this.noteLength < 2000 && charCountEl.classList.contains('error')) {
            charCountEl.classList.remove('error')
          }
        }

        this.tip.amount = parseInt(this.tip.amount);

        // hacky af
        var drinkRequestMsg = document.getElementById('drinkRequestMsg')
        if (drinkRequestMsg) {
          this.sizeTextarea(drinkRequestMsg)
        }
      },
      tipBartender: function() {
        this.validateForm();

        if (this.formIsValid) {
          var msg = this.tip.getMsg(this.drinkRequest);
          window.location = venmo(this.tip.recipient, parseInt(this.tip.amount), msg);
        }
      },

      sizeTextarea: function(textarea) {
        textarea.style.cssText += 'height:' + textarea.scrollHeight + 'px;'
      },
      filterInput: function(event) {
        if (event.key == 'Enter') {
          event.preventDefault();
        }
      },
      getBartenderName: function()
      {
        return this.bartender.name
      }
    },
    created: function() {
      if (!this.$isMobile()) {
        var msg = '<b>Heads up!</b> You need to be using a device with the Venmo app installed in order to tip ' + this.bartenderFirstName() + '.'
        this.flash(msg, 'error', { important: 'true' })
      }
    }
  }

</script>

