export class Bartender {
  constructor(name, venmoUsername, tipAmount) {
    this.name = name;
    this.venmoUsername = venmoUsername;
    this.tipAmount = parseInt(tipAmount);
  }

  toString() {
    return this.name + "|" + this.venmoUsername + "|" + this.tipAmount;
  }

  encode() {
    return encodeURIComponent(btoa(this));
  }

  static decode(encodedBartender) {
    var params = atob(decodeURIComponent(encodedBartender)).split("|");
    if (params.length != 3 || !parseInt(params[2])) {
      throw 'Invalid bartender link';
    }

    return new Bartender(params[0], params[1], parseInt(params[2]));
  }

}

export class Tip {
  constructor(bartender) {
    this.recipient = bartender.venmoUsername;
    this.amount = bartender.tipAmount;
    this.msg = "Cheers!";
  }

  getMsg(drinkRequest) {
    var msg = this.msg;

    if (drinkRequest.enabled)
    {
      msg = msg + ' ' + drinkRequest.getMsg();
    }

    return msg;
  }
  // execute() {
  //   return "venmo://paycharge?txn=pay&recipients="+this.recipient+"&amount="+this.amount+"&note="+encodeURIComponent(this.msg + this.drinkRequest.msg + " " + this.drinkRequest.ingredients.join(', '));
  // }
}

export class DrinkRequest {
  constructor() {
    this.enabled = false;
    this.msg = "Could you help me make a drink with what I've got at home?";
    this.ingredients = [];
  }

  getMsg() {
    if (this.ingredients.length) {

      var last_index = this.ingredients.length - 1;
      var ingredientsMsg = '';
      var i;

      for (i=0;i<this.ingredients.length;i++) {
        if (i == last_index && !this.ingredients[i].startsWith('and ') && last_index != 0) {
          ingredientsMsg += 'and ';
        }

        ingredientsMsg += this.ingredients[i];

        if (i != last_index) {
          if (this.ingredients.length > 2) {
            ingredientsMsg += ', ';
          } else {
            ingredientsMsg += ' ';
          }
        } else if (!this.ingredients[i].endsWith('.')) {
          ingredientsMsg += '.';
        }
      }

      return this.msg + ' Right now, I have ' + ingredientsMsg;

    } else {
      return this.msg;
    }

  }

}


export function venmo(recipient, amount, note) {
  if (note.length < 1978) {
    note += `

🍻 via eightysixbar.com`
  }

  return "venmo://paycharge?txn=pay&recipients=" + recipient + "&amount=" + amount + "&note=" + encodeURIComponent(note);
}
